class UnitsFormFieldComponent {
    constructor(unitsService, dialog) {
        this.unitsService = unitsService;
        this.dialog = dialog;
        this.unitsFetching = true;
        this.stockable = false;
    }
    ngOnInit() {
        this.getUnits();
    }
    getUnits() {
        this.unitsFetching = true;
        this.unitsService.getAllUnit({}).then(unitsObjects => {
            this.units = of(JSON.parse(JSON.stringify(unitsObjects)));
            this.unitsFetching = false;
        }).catch(reason => {
            this.units = of([{ name: 'No unit' }]);
            console.warn(reason);
            this.unitsFetching = false;
        });
    }
    addNewUnit($event) {
        $event.preventDefault();
        $event.stopPropagation();
        this.dialog.open(DialogUnitNewComponent, {
            closeOnNavigation: true
        }).afterClosed().subscribe(value => {
            if (value) {
                this.getUnits();
            }
        });
    }
    refreshUnits($event) {
        $event.preventDefault();
        $event.stopPropagation();
        this.getUnits();
    }
}
